<!-- 个人信息页面 -->
<template>
    <div class="box">
        <el-form :label-position="labelPosition" label-width="80px" :model="personalForm" :rules="rules" ref="personalForm">
            <el-form-item label="头像">
                <el-upload class="avatar-uploader" :action="``" :on-change="imgChange" :show-file-list="false">
                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                    <i class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
            </el-form-item>
            <el-form-item label="用户名" required prop="userName">
                <el-input v-model="personalForm.userName"></el-input>
            </el-form-item>
            <el-form-item label="所属公司" required prop="userCompany">
                <el-input v-model="personalForm.userCompany"></el-input>
            </el-form-item>
            <el-form-item label="密码" required prop="password">
                <el-input v-model="personalForm.password"></el-input>
            </el-form-item>
            <el-form-item label="电话号码" required prop="userPhone">
                <el-input v-model="personalForm.userPhone"></el-input>
            </el-form-item>
            <el-form-item label="性别" required>
                <el-radio-group v-model="sex" class="radio">
                    <el-radio :label="'男'">男</el-radio>
                    <el-radio :label="'女'">女</el-radio>
                </el-radio-group>
            </el-form-item>
            <div class="sure">
                <el-button type="primary" class="sure" @click="updateInfo('personalForm')">确定修改</el-button>
            </div>

        </el-form>
    </div>
</template>

<script>
import { reqUserImg } from '@/api/index';
export default {
    name: 'personalInfo',
    data() {
        return {
            imgFile: null,
            imageUrl: null,
            labelPosition: 'right',
            personalForm: {
                password: "",
                userPhone: "",
                userName: "",
                userCompany: ''
            },
            rules: {
                password: [
                    { trigger: 'blur', required: true,message:'请输入密码' }
                ],
                userPhone: [
                    {  trigger: 'blur', required: true,message:'请输入电话号码' }
                ],
                userName: [
                    { trigger: 'blur', required: true,message:'请输入用户名' }
                ],
                userCompany: [
                    {  trigger: 'blur', required: true,message:'请输入公司名称' }
                ]
            },
            sex: '男'
        };
    },
    methods: {
        //头像处理
        imgChange(file) {
            this.imgFile = file
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        async uploadImg(file) {
            try {
                const response = await reqUserImg(localStorage.getItem('id'), file.raw);
                console.log(response);
            } catch (error) {
                console.error(error);
            }
        },
        updateInfo(personalForm) {
            this.$refs[personalForm].validate((valid) => {
                if (valid) {
                    this.$confirm('此操作将修改个人信息, 是否确认?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        const { userName, userCompany, password, userPhone } = this.personalForm
                        const id = localStorage.getItem('id')
                        const { sex } = this
                        const form = { id, userName, userCompany, password, userPhone, sex }
                        this.$store.dispatch("updateInfo", { form, vm: this })
                        this.uploadImg(this.imgFile)
                        this.$store.dispatch("getUserInfo", localStorage.getItem('id'))
                    })
                } else {
                }
            });


        }
    },
    mounted() {

    }
}
</script>

<style lang="scss" scoped>
.box {
    width: 50%;
    height: 700px;
    margin-left: 200px;
    margin-top: 100px;

    .el-form-item {
        margin-bottom: 50px;

        .avatar-uploader {
            width: 100px;
            height: 100px;
            border: 1px dashed #d9d9d9;
            border-radius: 6px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
        }

        .avatar-uploader .el-upload:hover {
            border-color: #409EFF;
        }

        .avatar-uploader-icon {
            font-size: 28px;
            color: #8c939d;
            width: 100px;
            height: 100px;
            line-height: 100px;
            text-align: center;
        }

        .avatar {
            width: 100px;
            height: 100px;
            display: block;

        }
    }

    .radio {
        transform: scale(1.2);
        margin-left: 50px;
    }

    .sure {
        width: 100px;
        height: 50px;
        margin: 0 auto;
    }
}
</style>